import { useState } from "react"

function Banner () {
  const [banner, setBanner] = useState(true)

  if (banner)
    return (
      <div className="w3-container w3-border-bottom flex sticky">
        <span className="">
          
          
        </span>
          <button
          className="w3-button"
          onClick={() => setBanner(false)}>
            <i class="fa-solid fa-x"></i>
            </button>
        </div>
    )
}


export default Banner